<template>
  <div id="editDepartment">
    <v-row justify="center">
      <!-- <v-btn color="primary" dark @click.stop="aaa" class="ml-3 mr-4 mt-1 mb-1" wi>增加部门</v-btn> -->
      <v-icon title="编辑部门" class="ml-6" v-text="icon" @click.stop="isSelectDept" />
      <v-dialog v-model="dialog" persistent max-width="600px">
        <v-card>
          <v-card-title>
            <span class="headline">编辑部门</span>
          </v-card-title>
          <v-card-text>
            <v-form ref="form" v-model="valid" :lazy-validation="lazy">
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="部门名称(*必填)"
                    required
                    v-model="departmentObj.name"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="部门编码(*必填)"
                    required
                    v-model="departmentObj.code"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :rules="nameRules"
                    label="排序号(*必填)"
                    required
                    v-model="departmentObj.numIndex"
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field label="部门描述" aria-multiline v-model="departmentObj.description" />
                </v-col>
                <v-col cols="12">
                  <label>上级部门：</label>
                  <v-treeview
                    :active.sync="active"
                    :items="departments"
                    :open.sync="open"
                    color="warning"
                    return-object
                    hoverable
                    transition
                    activatable
                  >
                    <template v-slot:prepend="{ open }">
                      <v-icon
                        :color="open ? 'green' : 'purple'"
                      >{{open ? 'mdi-file-tree-outline' : 'mdi-file-tree'}}</v-icon>
                    </template>
                  </v-treeview>
                </v-col>
              </v-row>
            </v-form>
            <!-- <small>*indicates required field</small> -->
          </v-card-text>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn color="blue darken-1" text @click="dialog = false">关闭</v-btn>

            <v-btn color="blue darken-1" :disabled="!valid" text @click="doUpdate()">保存</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>
    </v-row>
  </div>
</template>

<script>
export default {
  data: () => ({
    active: [],
    avatar: null,
    open: [],
    selectable: true,
    
    color: "orange",
    icon: "mdi-pencil",

    lazy: false,
    valid: true,
    dialog: false,

    departmentObj: {
      name: "",
      code: "",
      numIndex: "",
      description: ""
    },

    //表单验证
    nameRules: [v => !!v || "该选项为必填字段"]
  }),
  props: {
    parentDeptObj: {
      type: Object
    },
    departments: {
      type: Array
    }
  },
  watch: {
    parentDeptObj(val) {
      if (val) {
        // this.active.push(val);
        this.departmentObj = Object.assign({}, val);
        this.open = [];
        this.pushOpenNodes(this.departmentObj);
      }
    },
  },
  mounted() {
    
  },

  methods: {
    doUpdate() {
      //1. 发送编辑请求
      let postData = {
        id: this.departmentObj.id,
        uniqueCode: this.departmentObj.uniqueCode,
        departmentName: this.departmentObj.name,
        departmentCode: this.departmentObj.code,
        numIndex: this.departmentObj.numIndex,
        description: this.departmentObj.description,
        parentUniqueCode: this.active[0].uniqueCode,
      };
      this.$http
        .post("/user/api/v1/department/updateDepartment", postData)
        .then(response => {
          if (response.success) {
            response.msg = "部门更新成功";
            //2. 回调向父组件传值
            this.$emit("editOrg");
          }
          this.$store.commit("SET_SNACK", response);
        })
        .catch(error => {
          console.log(error);
        });

      this.dialog = false;
    },

    isSelectDept() {
      if (!this.parentDeptObj) {
        this.dialog = false;
        this.$store.commit("SET_SNACK", {
          success: false,
          msg: "请先选择部门"
        });
      } else {
        this.dialog = true;
      }
    },

    pushOpenNodes(selection) {
      let _this = this;
      let parentIds = selection.parentIds;
      if(!!parentIds) {
        let parentNodes = this.departments;
        parentIds.forEach(function(currentParentNodeId, index) {
          if(!!parentNodes && parentNodes.length > 0){
            parentNodes = _this.pushOpenNodesChildren(parentNodes, currentParentNodeId);
            if((parentIds.length -1) == index) {
              _this.active=[];
              _this.active.push(_this.open[_this.open.length - 1]);
            }
          }
        });
      }
    },

    pushOpenNodesChildren(parentNodes, currentParentNodeId) {
      let _this = this;
      let results = [];

      if(!parentNodes){
        return [];
      }

      parentNodes.forEach(function(selection, index){
          if(selection.id == currentParentNodeId) {
            _this.open.push(selection);
            results = selection.children;
            return;
          }
      });

      return results;
    },
  }
};
</script>

